	box-sizing: border-box;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
	margin: 0 auto;
	border-spacing: 0;
	border-color: #fff;
	top: 0;
	display: table;
	width: 5px;
	height: 11px;
	border-width: 2px;
	border-style: solid;
	border-top: 0;
	border-left: 0;
	content: '';
}
